* {
  font-family: "Source Sans Pro", sans-serif;
}

html {
  box-sizing: border-box;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

ul[class],
ol[class] {
  padding: 0;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol,
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
}

ul[class] {
  list-style: none;
}

img {
  max-width: 100%;
  display: block;
}

input,
button,
textarea,
select {
  font: inherit;
}

a {
  text-decoration: none;
}

* {
  margin: 0;
  padding: 0;
}

html,
body {
  height: 100%;
}

body {
  background-color: rgb(250, 250, 250);
}

.wrapper {
  display: flex;
  flex-direction: column;
  height: 100%;
}

.content {
  flex: 1 0 auto;
}

.main {
  flex: 1 0 auto;
}

.footer {
  flex: 0 0 auto;
}

.container {
  max-width: 1160px;
  margin: 0 auto;
  padding: 0 15px;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.header {
  background-color: rgb(240, 235, 226);
}
.header__inner {
  display: flex;
  justify-content: center;
  height: 50px;
  align-items: center;
}

.logo {
  color: rgba(58, 58, 58, 0.714);
  font-size: 25px;
  font-weight: 700;
}

.title {
  display: flex;
  align-items: center;
  color: rgb(240, 235, 226);
  justify-content: left;
  font-weight: 900;
  font-size: 27px;
  width: 350px;
  height: 40px;
  color: rgba(58, 58, 58, 0.714);
  margin-left: 25px;
}

.subtitle {
  color: rgba(58, 58, 58, 0.714);
  font-size: 18px;
  margin-left: 25px;
}

.title-subtitle {
  line-height: 18px;
}

.main__title {
  display: flex;
  justify-content: center;
  flex-direction: column;
  padding: 0 0 8px 0;
  background-color: rgb(245, 245, 240);
}
.main__form {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  margin: 0;
  background-color: rgb(245, 245, 240);
}
.main__inner {
  display: flex;
  justify-content: center;
  flex-direction: column;
  border: 1px solid;
  border-radius: 5px;
  border-color: rgb(184, 171, 148);
}
.main__input {
  width: 350px;
  padding: 10px 0 10px 20px;
  margin: 5px 0;
  background-color: rgb(250, 250, 250);
  border: 1px solid rgba(184, 171, 148, 0.7);
  border-radius: 4px;
  outline-color: rgba(78, 78, 78, 0.714);
}
.main__enter {
  width: 350px;
  height: 50px;
  margin: 5px 0;
  color: rgb(240, 235, 226);
  background-color: rgba(58, 58, 58, 0.714);
  border: unset;
  border-radius: 28px;
  font-weight: 900;
  font-size: 22px;
  letter-spacing: 0.01em;
  cursor: pointer;
}
.main__enter:hover {
  background-color: rgba(44, 44, 44, 0.714);
}
.main__question {
  margin: 5px 0;
  color: rgba(58, 58, 58, 0.714);
  font-size: 18px;
}
.main__connect {
  color: rgb(240, 235, 226);
  background-color: rgba(58, 58, 58, 0.714);
  border: unset;
  border-radius: 4px;
  width: 120px;
  font-weight: 900;
  height: 35px;
  background-color: rgb(77, 134, 77);
  cursor: pointer;
}
.main__connect:hover {
  background-color: rgb(69, 122, 69);
}
.main__msg {
  color: rgba(44, 44, 44, 0.714);
  font-size: 18px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 350px;
  height: 50px;
  margin: 0;
  border: 2px solid rgb(212, 128, 49);
  border-radius: 4px;
  font-weight: 600;
}

.footer {
  display: flex;
  justify-content: center;
  height: 50px;
  align-items: center;
}
.footer__down {
  text-align: center;
  height: 45px;
}
.footer__logo {
  text-align: center;
  color: rgba(58, 58, 58, 0.714);
  font-size: 25px;
  font-weight: 700;
}